    const formVal = require("@/common/formValidation.js")
    import cityPicker from '@/components/w-picker/w-picker.vue'
    import uniListItem from '@/components/uni-ui/uni-list-item/uni-list-item.vue'
	export default {
        components:{
            cityPicker,
            uniListItem
        },
		data() {
			return {
                form: {
                    username: "",
                    phoneNumber: "",
                    area: "",
                    address: "",
                    isDefault: true
                }
			}
		},
        onLoad() {
            
        },
		methods: {
			picker() {
                this.$refs.picker.show();
            },
            pick(e) {
                this.form.area=e.result
            },
            confirm() {
                let form = this.form;
                let rules = [
                    {
                        name: "username",
                        rule: ["required", "minLength:2"],
                        msg: ["请输入收货人姓名","姓名必须2个字符以上"]
                    },
                    {
                        name: "phoneNumber",
                        rule: ['required', 'isMobile'],
                        msg: ['请输入手机号', '请输入合法的手机号']
                    },
                    {
                        name: "area",
                        rule: ['required'],
                        msg: ['请输入您的地址']
                    },
                    {
                        name: "address",
                        rule: ['required'],
                        msg: ['请输入您的详细收货地址']
                    }
                ]
                let checkRes = formVal.validation(form,rules);
                if(!checkRes) {
                    uni.showToast({
                        title: "保存成功"
                    })
                } else {
                    uni.showToast({
                        title: checkRes
                    })
                }
            }
		}
	}
